<template>
  <div>
    <pageTop title="大V"></pageTop>

    <div class="searchBox">
      <el-input v-model="input"
                style="width:500px;height:40px"
                placeholder="请输入大V名">
        <el-button slot="append"
                   icon="el-icon-search"
                   style="color:#409EFF"
                   @click="search"></el-button>
      </el-input>
      <!-- <el-button type="primary"
                 icon="el-icon-search">搜索</el-button> -->
    </div>
    <el-divider></el-divider>

    <div class="infinite-list-wrapper"
         style="overflow:auto;height:calc(100vh - 153px)">
      <el-scrollbar style="height:100%; ">
        <loading1></loading1>
        <!-- v-infinite-scroll="load" -->
        <ul class="list"
            infinite-scroll-disabled="disabled"
            v-infinite-scroll="load">
          <li v-for="item in data"
              :key="item.name"
              class="list-item">

            <div>
              <div class="img">
                <!-- <img src=""
                   alt=""> -->
                <i class="el-icon-sunny"
                   style="font-size:40px
              "></i>
              </div>
              <div class="content">
                <router-link :to="{name:'userMainPage','query':{userOnlyId:item.userOnlyId}}">
                  <div class="name">{{item.name}}</div>
                </router-link>
                <div class="email">E-mail:{{item.email}}</div>
                <div class="summary">简介:{{item.summary}}</div>
                <div class="blog">
                  <div v-for="i in item.blog"
                       :key="i.blogTitle">
                    <img :src="img"
                         alt="">
                    <span class="title">
                      <a href="">{{i.blogTitle}}</a>
                      <span class="date">{{i.created_at}}</span>
                    </span>
                    <!-- <a style="display:inline-block">{{i}}我是博客的标题,我-需要长点，排两行，哈哈哈哈哈哈哈</a> -->
                  </div>
                  <div class="total">
                    <router-link :to="{name:'userMainPage','query':{userOnlyId:item.userOnlyId}}">
                      <span v-if="item.blogcount>0">全部{{item.blogcount}}个博客></span>
                    </router-link>
                  </div>
                </div>
              </div>

            </div>
            <el-divider></el-divider>
          </li>
        </ul>

        <el-backtop target=".infinite-list-wrapper"></el-backtop>
      </el-scrollbar>
    </div>

  </div>
</template> 

<script>
import loading1 from '../components/loading/loading'
import con3 from "@/assets/images/topSample1.jpg";
import top from '../components/top'
export default {
  data () {
    return {
      count: 10,
      loading: false,
      api_token: "",
      index: 1,
      pagecount: 5,
      userName: "",
      data: [],
      img: con3,
      input: "",
      disabled: false
    }
  },
  components: {
    pageTop: top,
    loading1
  },
  computed: {


  },
  mounted () {
    if (window.sessionStorage.getItem('apiToken')) {
      this.api_token = window.sessionStorage.getItem('apiToken')
    } else {
      this.$router.push('login')
    }

  },
  methods: {
    load () {

      this.loading = true

      this.$API.getBigVList({ api_token: this.api_token, index: this.index, pagecount: this.pagecount, userName: this.input }).then(res => {
        if (res.data.userNum <= this.index * this.pagecount) {

          this.disabled = true
        }

        this.data = this.data.concat(res.data.data)


      }
      ).catch(e => { console.log(e) })
      this.index++;

    },
    search () {
      this.index = 1
      this.data = []
      this.load();
      this.noMore = false;
    }
  }
}
</script>


<style lang='less' scoped>
.searchBox {
  /deep/.el-button:hover {
    color: red;
  }
}
.infinite-list-wrapper {
  height: 100%;
}
.list-item {
  position: relative;
  margin: 0 auto;
  text-align: left;
  width: 1050px;
  .img {
    display: inline-block;
    position: absolute;
    top: 0;

    height: 50px;
    width: 50px;
  }
  .content {
    text-align: left;
    margin-left: 50px;
    display: inline-block;
    color: #606266;
    width: 1000px;
    .name {
      font-weight: 800;
      font-size: 20px;
      color: #303133;
    }
    .email {
      padding-top: 5px;
    }
    .summary {
      padding-top: 5px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .blog {
      padding-top: 5px;
      color: #303133;
      display: flex;
      justify-content: space-between;
      div {
        height: 50px;
        display: inline-block;
      }
      img {
        width: 80px;
        height: 50px;
        display: inline-block;
        // vertical-align: top;
      }
      .title {
        display: inline-block;
        width: 150px;
        padding-left: 10px;
        line-height: 15px;
        height: 50px;
        overflow: hidden;
        position: relative;
        a {
          display: block;
          width: 80px;
          height: 30px;
          overflow: hidden;
        }
        .date {
          color: #606266;
          font-size: 12px;
          position: absolute;
          bottom: 0;
        }
      }
      div.total {
        position: relative;
        width: 150px;
        span {
          position: absolute;
          bottom: 0;
          color: #409eff;
          cursor: pointer;
        }
      }
    }
  }
}

.infinite-list-wrapper::-webkit-scrollbar {
  display: none;
}

/deep/ .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>


